.materiasFrontendDH{
    width: 100%;
    height: 100vh;
    background-color: black;
}

.carouselMateriasFrontend{

    width: 100%;
    height: 90vh;
    
}

.carouselMateriasFrontend ul{
    padding-top: 15vh;
    height: 85vh;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    

}

.objetoFilaFrontend{
    position: relative;
    width: 90%;
    height: 75vh;
    margin-left: 5%;
    margin-right: 5%;
}

.tarjetaFrontend{

    width: 80%;
    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;

    border: 0.3vw white solid;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; 
}

.tarjetaFrontend:hover{
    cursor: pointer;
}

.divTituloCardFrontend{
    position: absolute;
    width: 100%;
    text-align: center;
    margin-top: 5%;
}

.tituloCardFrontend{
    color: white;
    font-family: "AvantGarde";
    font-size: 4.5vh;
}

.contenedorImagenFront1{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 100%;
    height: 60%;
}

.materiaFront1, .reactIcon{
    width: 100%;
    height: 100%;
}



.reactIcon{
    filter: grayscale(90%);
}

.front1Icon{
    width: 55%;
    height: 65%;
}

.htmlIcon{
    position: absolute;
    margin-left: 2.5%;
    top: 0;
    left: 0;
    z-index: 2;
}

.cssIcon{
    position: absolute;
    margin-right: 2.5%;
    bottom: 0;
    right: 0;
    z-index: 1;
}

.linkToContainer{
    width: 50px;
    height: 50px;
}

.tarjetaFrontend:hover > .contenedorImagenFront1 > .divWarnIcon{
    filter: grayscale(0%);
    transition: 0.5s;
}