.homeMaterias {
    width: 100%;
    height: 100vh;
    background-color: black;
}

.carouselMaterias{

    width: 100%;
    height: 90vh;
    
}

.carouselMaterias ul{
    padding-top: 15vh;
    height: 85vh;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}

.carouselMaterias ul li{
    height: fit-content;
}

img:hover{
    cursor: pointer;
}


.materia{
    height: 75vh;
}

.divTituloCardMateria{
    position: absolute;
    
    height: fit-content;
    text-align: center;
    width: 100%;    
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    
}

.tituloCardMateria{
    color: white;
    position: relative;
    text-align: center;
    vertical-align: middle;

    -webkit-text-stroke: 2px black;
    font-family: "AvantGarde";
    font-size: 7.5vh;
    
    z-index: 6;
   
}

@font-face {
    font-family: AvantGarde;
    src: url("../fonts/avant-garde.woff");
}

.materiaTopContainer{
    width: 90%;
    height: 100%;
    margin-left: 5%;

    border: 4px white solid;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; 
}

.materiaTopContainer:hover{
    cursor: pointer;
}

.materiaTopContainer:hover > .divWarnIcon{
    filter: grayscale(0%);
    transition: 0.5s;  
}

.materiaImg{
    height:100%;
    width: 90%;
    margin-left: 5%;
    border: 4px white solid;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; 
}


.divWarnIcon{
    transition: 0.5s;
    filter: grayscale(100%);
    position: absolute;
    height: 50% !important;
    width: 60% !important;
    margin: auto !important;
    top: 0 !important;
    bottom: 0 !important;
    left: 0 !important;
    right: 0 !important;
    z-index: 2;
}

.imgWarnIcon{
    height: 100% !important;
    width: 100% !important;
    margin-left: 0 !important;

}

.backConstruction{
    filter: grayscale(100%);
    width: 100%;
    height: 100%;
}

.inWorking{
    height: 100%;
}

.underConstructMateria{
    position: absolute;
    z-index: 5;
    width: 90%;
    height: 100%;
    background-color: gray;

}

.underConstruction{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    height: 50%;
}

.stopIcon{
    position: absolute;
    z-index: 1;
    width: 60%;
    left: 05%;
    height: 100%;
 
}

.workerIcon{
    position: absolute;
    z-index: 2;
    width: 60%;
    right: 5%;
    height: 100%;
}

.stopIconImg{
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}

.workerIconImg{
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}