
.tarjeta{
   
    border-color: red;
    border-width: 3px;
    border-style: solid;
    width: 85%;
    height: 75vh;
    border-radius: 25px;
    transition: 0.75s;
    filter: grayscale(100%);
    margin-left: 7.5%;
    
}

.tarjeta h2{
    text-align: center;
    color: white;
    height: 100%;
    width: 100%;
}

.divCardTitle{
    height: 10%;
    position: absolute;
    width: 100%;
    text-align: center;
    margin-top: 4%;
}

.cardTitle{
    font-size: 4vh;
}

.divCardImage{
    height: 90%;
}


.imagenTarjeta{
    width: 100%;
    
    position: absolute; /*Can also be `fixed`*/
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        /*Solves a problem in which the content is being cut when the div is smaller than its' wrapper:*/
        max-width: 100%;
        max-height: 100%;
        overflow: auto;
}

.tarjeta:hover{
    
    transform: scale(1.05);
    filter: grayscale(0%);
    
}