
.Header{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 10vh;
    background-color: dimgray;
    display: table;
  }
  
  a{
    color: white;
  }

  .Header a:hover{
    color: white;
  }
    
  .Header > *{
    display: table-cell;
    table-layout: fixed;
    vertical-align: middle;

  }
  
  .headerButtons{
    text-align:left;
    z-index: 2;
    width: 10%;
    font-size: 5vh;
  }

  #headerButtonId{
    
    margin-left: 15%;
    z-index: 5;
  }
  
  .Titulo{
    z-index: -1;
    color: white;
    text-align: center;
    width: 80%;
    }
  
    .Titulo h1{
      font-size: 5vh;
    }

  .relleno{
    text-align:left;
    visibility: hidden;
  }